/* Grid (View All mode) */
@import "~antd/dist/antd.css";
/** Colors **/
.ant-divider-horizontal.ant-divider-with-text-center::before,
.ant-divider-horizontal.ant-divider-with-text-left::before,
.ant-divider-horizontal.ant-divider-with-text-right::before,
.ant-divider-horizontal.ant-divider-with-text-center::after,
.ant-divider-horizontal.ant-divider-with-text-left::after,
.ant-divider-horizontal.ant-divider-with-text-right::after {
  border-top-color: #cecece !important;
}

.ls-renderall {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.ls-renderall > div {
  width: 49%;
  margin-right: 1%;
}

.ls-fade {
  opacity: 1;
  animation-name: fadeInOpacity;
  animation-iteration-count: 1;
  animation-timing-function: ease-in;
  animation-duration: 1s;
}

.htx-highlight > .htx-highlight {
  padding: 2px;
}

.htx-no-label:after {
  display: none;
}

.htx-highlight-last:after {
  margin-left: 2px;
}

.htx-yoyo:after {
  content: "Yoyo";
}

@keyframes fadeInOpacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.ant-list-item-action {
  margin-left: 10px !important;
}

.ant-modal-confirm-error ul {
  padding-left: 1.5em;
}

.react-dropdown-tree-select .node > .toggle {
  font-family: monospace;
  font-style: normal;
  color: initial;
  cursor: pointer;
}

.react-dropdown-tree-select .node.disabled > .toggle {
  padding-right: 100%;
  margin-right: -100%;
  position: relative;
  z-index: 1;
  user-select: none;
}

.react-dropdown-tree-select .node.disabled > label {
  color: initial;
  cursor: default;
}

.react-dropdown-tree-select .node.disabled > label > input {
  opacity: 0;
}

.grid {
  flex-grow: 1;
  display: grid;
  grid-auto-columns: calc(50% - 15px);
  grid-auto-flow: column;
  grid-column-gap: 30px;
  margin: 0 30px;
  overflow: auto;
  position: relative;
}
@media (max-width: 1200px) {
  .grid {
    grid-auto-columns: 100%;
  }
}

.grid > div > h4 {
  cursor: pointer;
}

.container {
  position: relative;
  flex-grow: 1;
  min-width: 0;
}

.container > button {
  position: absolute;
  top: 0;
  width: 30px;
  height: 100%;
}
.container > button:hover {
  background: #eee;
}

.container > button.left {
  left: 0;
}

.container > button.right {
  right: 0;
}

.grid > div:global(.hover) {
  background: #eee;
}
.grid > div:global(.hover) h4 {
  color: #1890ff;
}

/* don't let the empty blocks to break the nice grid */
.grid > div:empty {
  display: none;
}