.taxonomy {
  border: 1px solid #d9d9d9;
  border-radius: 6px;
  padding: 4px 4px 0;
  width: max-content;
  max-width: 100%;
  position: relative;
}
.taxonomy > span {
  display: flex;
  align-items: center;
  line-height: 30px;
  padding: 0 4px;
  margin-bottom: 4px;
  white-space: nowrap;
  cursor: pointer;
}

.taxonomy > span > svg {
  transform: rotate(-180deg);
}

.taxonomy_open > span > svg {
  transform: none;
}

.taxonomy__selected {
  display: flex;
  flex-wrap: wrap;
  min-height: 28px;
}
.taxonomy__selected div {
  margin: 0 2px 4px 0;
  background: #f2f2f2;
  padding: 0;
  border-radius: 4px;
  display: flex;
  align-items: center;
}
.taxonomy__selected div span {
  padding-inline: 8px;
}
.taxonomy__selected input[type=button] {
  border: none;
  background: none;
  cursor: pointer;
  width: 24px;
  height: 24px;
  padding: 0;
  line-height: 1;
  font-weight: 500;
  font-size: 20px;
  color: #09f;
  margin-left: -8px;
}
.taxonomy__selected input[type=button]:hover {
  color: red;
}

.taxonomy__dropdown {
  position: absolute;
  z-index: 10;
  background: white;
  border-radius: 6px;
  left: -1px;
  border: 1px solid #e2e2e2;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.12);
}
.taxonomy__dropdown input[type=checkbox] {
  margin-right: 4px;
  font-size: 20px;
  line-height: 30px;
  width: 0.7em;
  height: 1.4em;
}
.taxonomy__dropdown input[type=checkbox]:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.taxonomy__search {
  width: 100%;
  border: none;
  border-bottom: 1px solid #e2e2e2;
  border-radius: 3px 3px 0 0;
  padding: 6px 16px;
  margin-bottom: 4px;
  box-shadow: 0 -2px 0 6px white;
  position: sticky;
  top: 0;
  z-index: 1;
}
.taxonomy__search:focus, .taxonomy__search:focus-visible {
  outline: 1px solid #09f;
  border-bottom-color: #09f;
}

.taxonomy__item ~ div {
  margin-left: 16px;
  width: calc(100% - 100px);
}

.taxonomy__measure {
  visibility: hidden;
  position: absolute;
  pointer-events: none;
  display: flex;
  padding-left: 43px;
  padding-right: 60px;
}
.taxonomy__measure.taxonomy__measure_ff_dev4075 {
  padding-right: 8px;
  line-height: 2em;
}

.taxonomy__item {
  display: flex;
}
.taxonomy__item:focus-within {
  background: #eee;
}
.taxonomy__item_user, .taxonomy__item_session {
  font-style: italic;
}
.taxonomy__item_session {
  color: #09f;
}

.taxonomy__item label {
  overflow-wrap: break-word;
  flex: 1;
  flex-grow: 1;
  width: max-content;
  line-height: 2em;
  padding-right: 8px;
}

.taxonomy__grouping {
  font-family: monospace;
  font-size: 16px;
  display: inline-block;
  width: 24px;
  height: 24px;
  margin: 2px 3px 0 0;
  text-align: center;
  user-select: none;
  flex-shrink: 0;
  pointer-events: all;
}
.taxonomy__grouping svg {
  transition: transform 0.1s;
}
.taxonomy__grouping:not(:empty) {
  cursor: pointer;
}

.taxonomy__extra {
  margin-left: auto;
  margin-right: 5px;
  opacity: 0.6;
  padding: 0 12px;
  width: 40px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 40px;
  text-align: right;
  position: relative;
}
.taxonomy__extra_actions {
  visibility: hidden;
  display: block;
  font-weight: bold;
  cursor: pointer;
  text-align: center;
}
.taxonomy__item .taxonomy__extra_count:not(:last-child) {
  position: absolute;
}
.taxonomy__item:hover .taxonomy__extra_count:not(:last-child) {
  visibility: hidden;
  display: none;
}
.taxonomy__item:hover .taxonomy__extra_actions {
  visibility: visible;
  display: block;
}

.taxonomy__action {
  zoom: 1;
}

.taxonomy__add__container {
  margin-bottom: 8px;
}

.taxonomy__add {
  margin-left: 27px;
  display: flex;
  align-items: center;
  opacity: 0.6;
}
.taxonomy__add::before {
  content: "+";
  width: 16px;
  padding: 4px 0;
  text-align: center;
  font-size: 18px;
  line-height: 0;
}
.taxonomy__add button {
  flex-grow: 1;
  border: none;
  padding: 1px 4px;
  font-weight: normal;
  text-align: left;
}

.taxonomy__newitem {
  display: flex;
}
.taxonomy__newitem [name=taxonomy__add] {
  padding: 1px 2px;
  border: none;
  flex-grow: 1;
  margin-left: 45px;
  margin-right: 8px;
  font: inherit;
  font-style: italic;
  color: #09f;
  background: rgba(0, 0, 0, 0.03);
}

.taxonomy__collapsable {
  cursor: pointer;
  color: grey;
}