.phrase {
  background: #e8ffd0;
  padding: 0.5em;
  border-radius: 5px;
  margin-right: 5px;
  margin-bottom: 10px;
  display: table;
}
.phrase.numbered::before {
  counter-increment: phrase;
  content: counter(phrase);
  margin-right: 4px;
}

.name {
  background: transparent !important;
  margin-right: 5px;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently */
}

.name span {
  background: transparent !important;
  padding: 0px !important;
}

.text {
  zoom: 1;
}

.dialoguename {
  font-weight: bold;
  background: white !important;
  border-radius: 5px;
  padding: 5px;
  margin-right: 10px;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently */
}

.dialoguename span {
  background: white !important;
  padding: 0px !important;
}

.dialoguetext {
  zoom: 1;
  cursor: text;
}

.container {
  position: relative;
  overflow: auto;
  counter-reset: phrase;
}

.withAudio {
  margin-left: 36px;
}

.collapsed {
  display: block;
  max-width: max-content;
  height: 1px;
  padding: 2px;
  user-select: none;
}
.collapsed > * {
  visibility: hidden !important;
  opacity: 0;
  height: 1px;
  user-select: none;
}

.authorFilter {
  width: 100%;
  min-width: 320px;
  max-width: max-content;
  margin-bottom: 16px;
}
.authorFilter__showall {
  display: inline-flex;
  font-size: 14px;
  line-height: 32px;
}
.authorFilter__placeholder {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
}
.authorFilter__search {
  min-width: 318px;
}
.authorFilter__search__input {
  width: 100%;
  height: 40px;
  padding: 0 12px;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
  font-size: 14px;
  line-height: 22px;
  background: #fafafa;
  outline: none;
}
.authorFilter__select {
  display: flex;
  align-items: center;
  gap: 4px;
}
.authorFilter__select__item {
  color: initial;
  background-color: #ffffff;
  display: inline-flex;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  border-radius: 4px;
  padding: 3px 4px;
}

.audio {
  height: 32px;
  width: 100%;
  position: sticky;
  top: 0;
  z-index: 1;
}

.play {
  user-select: none;
  position: absolute;
  left: 0;
  margin-top: -0.3em;
  font-size: inherit;
}
.play:active path {
  fill: #1890ff;
}
.play:focus path:first-child {
  fill: #1890ff;
}